<!-- by 添少 -->
<template>
  <section class="container">
    <section style="height: 40px;z-index:9;position: relative;">
      <section ref="header" class="m-header">
        <v-Header class="head">
          <span slot='text' class="headText">编辑行程</span>
          <mt-button class="nextBtn" @click="handNext" slot="right">发送</mt-button>
        </v-Header>
      </section>
    </section>
    <mt-field placeholder="请输入标题" class="iptTitle" v-model="title"></mt-field>
    <mt-field placeholder="请输入摘要，建议简明扼要突出亮点，不超过25字。" type="textarea" rows="2" v-model="desc"></mt-field>
    <section ref="menu" id="m-menu">
      <section ref="menuBd" class="bd">
        <section id="toolbar-container">
          <select class="ql-size">
            <option selected></option>
            <option value="large"></option>
            <option value="huge"></option>
          </select>
          <button class="ql-bold" data-toggle="tooltip"></button>
          <button class="ql-italic" data-toggle="tooltip"></button>
          <button class="ql-underline" data-toggle="tooltip"></button>
          <button class="ql-strike" data-toggle="tooltip"></button>
          <button class="ql-image" data-toggle="tooltip"></button>
        </section>
        <section class="tip">
          <p>编辑好的内容请及时提交保存以免丢失.</p>
        </section>
      </section>
    </section>
    <div id="editor"></div>
  </section>
</template>
<script>
  import Header from '../../components/header';
  import { mapState } from 'vuex'
  import Quill from 'quill'
  import 'quill/dist/quill.snow.css'
  import b64toBlob from 'b64-to-blob'

  function validDataUrl (s) {
    return validDataUrl.regex.test(s)
  }

  validDataUrl.regex = /^\s*data:([a-z]+\/[a-z0-9-+.]+(;[a-z-]+=[a-z0-9-]+)?)?(;base64)?,([a-z0-9!$&',()*+;=\-._~:@\/?%\s]*?)\s*$/i

  let quill = null
  export default {
    name: '',
    components: { 'v-Header': Header,},
    props: {},
    data () {
      return {
        title: '',
        desc: '',
        content: '',
      }
    },
    watch: {},
    computed: {
      ...mapState({
        article_title: state => state.write.article.title,
        article_desc: state => state.write.article.desc,
        article_content: state => state.write.article.content || '',
      })
    },
    created () {
      this.init()
    },
    mounted () {
      quill = new Quill('#editor', {
        modules: {
          toolbar: {
            container: '#toolbar-container'
          }
        },
        placeholder: '请输入正文',
        theme: 'snow'
      })
      // console.log(quill)
      quill.container.firstChild.innerHTML = this.article_content

      let start = {
        time: 0,
        scrollTop: 0,
      }
      window.addEventListener('touchstart', e => {
        start.touchstart = {
          x: e.changedTouches[0].clientX,
          y: e.changedTouches[0].clientY,
        }
      })
      window.addEventListener('touchend', e => {
        if (e.changedTouches[0].clientX === start.touchstart.x &&
          e.changedTouches[0].clientY === start.touchstart.y) {
          // tap
          start.time = new Date()
          start.scrollTop = document.body.scrollTop
        }
      })
      window.addEventListener('scroll', ((e) => {
        if (new Date() - start.time < 200) {
          // 唤起键盘导致的滚动
          // document.title = quill.getLine()
          let scrollLen = document.body.scrollTop - start.scrollTop
          if (scrollLen > 0) {
            document.body.scrollTop -= scrollLen
          }
        }
        let header = this.$refs.header,
          menu = this.$refs.menu,
          menuBd = this.$refs.menuBd,
          scrollTop = document.body.scrollTop || document.documentElement.scrollTop
        if (scrollTop > 0) {
          header.classList.add('top')
          header.style.top = `${scrollTop}px`
        } else {
          header.classList.remove('top')
          header.style.top = `0px`
        }
        if (scrollTop > menu.offsetTop - 39) {
          menuBd.classList.add('top')
          menuBd.style.top = `${scrollTop + 39}px`
        } else {
          menuBd.classList.remove('top')
          menuBd.style.top = `0px`
        }
      }).bind(this))
    },
    methods: {
      init () {
        this.title = this.article_title
        this.desc = this.article_desc
      },
      initData () {
      },
      handNext () {
        this.setQuillContents().then(() => {
          this.content = quill.container.firstChild.innerHTML
          this.$store.commit(this.$actionType.set_article_data, {
            title: this.title,
            desc: this.desc,
            content: this.content
          })
          this.$router.push({
            path: 'preview',
            query: this.$route.query
          })
        })
      },
      setQuillContents () {
        return new Promise((resolve, reject) => {
          let quillContents = quill.getContents(), promises = []
          quillContents.forEach(e => {
            if (e.insert.hasOwnProperty('image')) {
              if (!e.hasOwnProperty('attributes')) {
                e.attributes = {}
              }
              e.attributes.width = '100%'
              if (validDataUrl(e.insert.image)) {
                //base64
                let b64 = e.insert.image.split(',')
                promises.push(new Promise((resolve, reject) => {
                  this.$commons.compressorImage(b64toBlob(b64[1], `image/${b64[0].split('/')[1].split(';')[0]}`)).then(res => {
                    this.$commons.uploadImage(res).then(res => {
                      if (res.code === 200) {
                        e.insert.image = res.data
                        resolve(res)
                      } else {
                        reject(res)
                      }
                    })
                  })
                }))
              }
            }
          })
          Promise.all(promises).then(arr => {
            quill.setContents(quillContents)
            resolve(arr)
          }).catch(arr => {
            reject(arr)
          })
        })
      },
    }
  }
</script>
<style lang="scss">

  #editor {
    font-size: 16px;
    height: auto;
    border-bottom-color: transparent;
    .ql-editor {
      img {
        width: 100% !important;
      }
    }
  }
</style>
<style lang="scss" scoped>

  .m-header {
    background-color: #fff;
    width: 100%;
    height: 40px;
    z-index: 9;
    left: 0;
    right: 0;
    &.top {
      position: absolute;
    }
  }

  .iptTitle {
    margin-top: -1px;
  }

  .ql-container.ql-snow,
  .ql-toolbar.ql-snow {
    border-left-color: transparent;
    border-right-color: transparent;
  }

  .tip {
    background-color: #FCF8E3;
    font-size: 14px;
    color: #8a6d3b;
    padding: 10px;
  }

  #m-menu {
    background-color: #fff;
    height: 86px;
    .bd {
      background-color: #fff;
      width: 100%;
      z-index: 19;
      left: 0;
      right: 0;
      &.top {
        position: absolute;
      }
    }
  }
</style>



